.Project {
    /* background-color: blue; */
    background-color: black;
    text-align: center;
    height: 100vh;
    width: auto;
    padding-top: 50px;
}
.Project-content {
    background-color: transparent;
    /* background-color: brown; */
    position: relative;
    height: 80vh;
    width: 80vw;
    top: 10vh;
    margin: auto;
}
.Project-arrow-left {
    position: absolute;
    z-index: 3;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    background-color: grey;
    color: white;
    left: -25px;
    top: calc(50% - 25px);
}
.Project-arrow-right {
    position: absolute;
    z-index: 3;
    height: 50px;
    line-height: 50px;
    width: 50px;
    left: calc(80vw - 25px);
    top: calc(50% - 25px);
    border-radius: 50%;
    background-color: grey;
    color: white;
}

.Project-carousel {
    position: relative;
    height: calc(80vh - 40px);
    background-color: rgb(25, 25, 25);
    border-radius: 35px 5px;
    box-shadow: 10px 5px rgb(90, 90, 90);
    background-image: linear-gradient(to right, rgba(0,0,0,0), rgba(100, 100, 100, 1));
}

.Project-Main {
    position: relative;
    /* background-color: red; */
    background-color: transparent;
    max-height: inherit;
    height: inherit;
    width: 70vw;
    margin: auto;
}

.Project-Navigation {
    /* background-color: olive; */
    background-color: transparent;
    height: 30px;
}
.Project-Domain {
    height: 50px;
    line-height: 50px;
    width: 70vw;
    /* background-color: tomato; */
    background-color: transparent;
    color: white;
    font-size: x-large;
}
.Project-Container {
    /* background-color: magenta; */
    background-color: transparent;
    min-height: 40vh;
    max-height: calc(80vh - 120px);
    width: auto;
    padding: 10px;
    display: grid;
    place-items: center;
}
.Project-Video {
    /* background-color: aquamarine; */
    background-color: transparent;
    height: 40vh;
    width: 60vw;
}

.Project-Description {
    color: white;
    white-space: pre-wrap;
    padding: 10px;
    /* font-size: 16px; */
}

.Project-Radio {
    height: 12px;
    width: 12px;
    border-radius: 50%;
    background-color: grey;
    display: inline-block;
    overflow: hidden;
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 10px;
}
.Project-Radio.active {
    background-color: white;
}

@media (min-width: 1200px) {
    .Project-content {
        width: 60vw;
    }
    .Project-arrow-right {
        left: calc(60vw - 25px);
    }
    .Project-Main {
        width: 50vw;
    }
    .Project-Domain {
        width: 50vw;
    }
    .Project-Video {
        width: 40vw;
    }
}