@import url('https://fonts.googleapis.com/css?family=Dancing+Script');

.Header {
    position: fixed;
    height: 50px;
    width: 100%;
    /* background-color: brown; */
    background-color: black;
    text-align: center;
    color: black;
    z-index: 10;
    padding: 5px;
}

.Header-heading {
    color: black;
    line-height: 50px;
    font-size: xx-large;
    font-family: 'Dancing Script', cursive;
    font-size: 45px;
}

.Header-heading.active {
    color: white;
}

.Header .Header-menu {
    display: flex;
    position: absolute;
    /* background-color: cadetblue; */
    background-color: grey;
    border-radius: 5px;
    color: white;
    width: 50px;
    height: 50px;
    top: 5px;
    right: 50px;
    justify-content: center;
    align-items: center;
}

@media (max-width: 800px) {
    .Header .Header-menu{
        right: 30px;
    }
}

.Header-menu.active {
    background-color: white;
    color: black;
}

.Header-drawer {
    position: absolute;
    background-color:grey;
    /* background-color: chocolate; */
    height: 100vh;
    top: 60px;
    width: 0px;
    right: 0%;
    overflow: hidden;
    transition: 0.5s;
    padding-left: 0px;
}

.Header-drawer.active {
    width: 200px;
}

.Header-drawer-contents {
    position: absolute;
    /* background-color:chartreuse; */
    background-color: transparent;
    width: 200px;
    height: 100vh;
    list-style-type: none;
    padding-left: 0px;
    padding-right: 0px;
}

.Header-drawer-contents li {
    position: relative;
    /* list-style: none; */
    width: 200px;
    
}

.Header-drawer-contents li:hover{
    background-color: rgb(40, 40, 40);
    width: 200px;
}

.Header-drawer-contents li a {
    position: relative;
    width: 100%;
    height: 50px;
    line-height: 50px;
    display: block;
    text-decoration: none;
    color: white;
    font-weight: 500;
    text-align: center;
}
@media (max-height: 500px) {
    .Header-drawer{
        height: 300px;
        min-height: 300px;
    }
    .Header-drawer-contents {
        height: 300px;
        min-height: 300px;
    }
    .Header-drawer-contents li a {
        height: 30px;
        line-height: 30px;
    }
    .Header-drawer-contents li {
        position: relative;
        width: 200px; 
    }
    .Header-drawer-contents li:hover{
        height: 30px;
    }
}