.Skill {
    /* background-color: blue; */
    background-color: black;
    text-align: center;
    height: 100vh;
    width: auto;
    padding-top: 50px;
}
.Skill-content {
    background-color: transparent;
    /* background-color: brown; */
    position: relative;
    height: 80vh;
    width: 80vw;
    top: 10vh;
    margin: auto;
}
.Skill-arrow-left {
    position: absolute;
    z-index: 3;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    background-color: grey;
    color: white;
    left: -25px;
    top: calc(50% - 25px);
}
.Skill-arrow-right {
    position: absolute;
    z-index: 3;
    height: 50px;
    line-height: 50px;
    width: 50px;
    left: calc(80vw - 25px);
    top: calc(50% - 25px);
    border-radius: 50%;
    background-color: grey;
    color: white;
}

.Skill-carousel {
    position: relative;
    height: calc(80vh - 40px);
    background-color: rgb(25, 25, 25);
    border-radius: 35px 5px;
    box-shadow: 10px 5px rgb(90, 90, 90);
    background-image: linear-gradient(to right, rgba(0,0,0,0), rgba(100, 100, 100, 1));
}

.Skill-Main {
    position: relative;
    /* background-color: yellow; */
    background-color: transparent;
    max-height: inherit;
    height: inherit;
    width: 70vw;
    margin: auto;
    flex-direction: row;
}

.Skill-Navigation {
    /* background-color: olive; */
    background-color: transparent;
    height: 30px;
}
.Skill-Domain {
    /* background-color: tomato; */
    background-color: transparent;
    color: white;
    height: 50px;
    line-height: 50px;
    width: 70vw;
    font-size: x-large;
}
.Skill-Container {
    /* background-color: magenta; */
    background-color: transparent;
    max-height: calc(80vh - 120px);
    width: auto;
    max-width: 70vw;
    padding: 10px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    place-items: center;
}

.Skill-Radio {
    height: 12px;
    width: 12px;
    border-radius: 50%;
    background-color: grey;
    display: inline-block;
    overflow: hidden;
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 10px;
}
.Skill-Radio.active {
    background-color: white;
}
.Skill-Outer {
    position: relative;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    margin-top: 10px;
    margin-bottom: 10px;
    display: grid;
    place-items: center;
}
.Skill-Inner {
    position: relative;
    /* background-color: blue; */
    background-color: black;
    color: white;
    /* line-height: 65px; */
    height: 70px;
    width: 70px;
    border-radius: 50%;
    display: grid;
    place-items: center;
}
@media (min-width: 1200px) {
    .Skill-content {
        width: 60vw;
    }
    .Skill-arrow-right {
        left: calc(60vw - 25px);
    }
    .Skill-Main {
        width: 50vw;
    }
    .Skill-Domain {
        width: 50vw;
    }
    .Skill-Outer {
        height: 100px;
        width: 100px;
        margin-right: 20px;
        margin-left: 20px;
    }
    .Skill-Inner {
        height: 90px;
        width: 90px;
        /* line-height: 85px; */
    }
}