@import url('https://fonts.googleapis.com/css?family=Dancing+Script');
.About-main {
    align-items: center;
    align-content: center;
    text-align: center;
    background-color:black;
    /* background-color: brown; */
    max-height: calc(100vh + 25vh);
    min-height: 100vh;
    width: auto;
}

.About-container {
    position: relative;
    background-color: black;
    border-radius: 25px 25px;
    box-shadow: rgba(255, 255, 255, 0.25) 0px 24px 25px, rgba(255, 255, 255, 0.12) 0px -12px 30px, rgba(255, 255, 255, 0.12) 0px 4px 6px, rgba(255, 255, 255, 0.17) 0px 12px 13px, rgba(255, 255, 255, 0.09) 0px -3px 5px;
    background-image: url("../../assets/paper.jpeg");
    display: block;
    top: 25vh;
    bottom: 25vh;
    margin-left: auto;
    margin-right: auto;
    width: 80vw;
    height: max-content;
    min-height: 40vh;
    text-align: center
}
@media (min-width: 1200px) {
    .About-container {
        width: 60vw;
    }
    
}

.About-heading {
    font-size: x-large;
    font-family: 'Dancing Script', cursive;
    animation-name: Title;
    animation-duration: 5s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-fill-mode: forwards;
}
@keyframes Title {
    from { 
        color: transparent;
    }
    to { 
        color: black;
    }
}
.About-text {
    font-size: 30px;
    font-weight: bolder;
    font-family: 'Dancing Script', cursive;
    white-space: pre-wrap;
    padding: 10px;
}