.Experience-main {
    align-items: center;
    align-content: center;
    text-align: center;
    /* background-color:cadetblue; */
    background-color:black;
    height: min-content;
    min-height: 100vh;
    padding-top: 50px;
    width: auto;
}

.Experience-content {
    /* background-color: blue; */
    background-color: transparent;
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    height: auto;
    width: auto;
    padding: 50px;
}

@media (max-width: 800px) {
    .Experience-content {
        flex-direction: column;
    }
}

.Experience-container {
    /* background-color: blueviolet; */
    background-color: transparent;
    position: relative;
    display: inline-block;
    min-height: max-content;
    width: 40vw;
    max-width: 40vw;
    flex-grow: 1;
    margin: auto;
    margin-top: 25px;
    margin-bottom: 25px;
}

@media (max-width: 800px) {
    .Experience-container {
        width: 70vw;
        max-width: 70vw;
    }
}

.Experience-logo {
    position: relative;
    background-color: grey;
    height: 100px;
    width: 100px;
    border-radius: 50%;
    left: calc(50% - 50px - 4px);
    top: 0px;
    z-index: 1;
    border: 4px solid white;
    overflow: hidden;
    transition: transform .2s;
}
.Experience-logo:hover {
    border: 4px solid grey;
    transform: scale(1.2);
}

.Experience-logo img {
    object-fit: cover;
    height: 100%;
    width: 100%;
}

.Experience-box {
    position: relative;
    background-color: rgb(25, 25, 25);
    border-radius: 35px 5px;
    box-shadow: 10px 5px rgb(90, 90, 90);
    background-image: linear-gradient(to right, rgba(0,0,0,0), rgba(100, 100, 100, 1));
    display: block;
    text-align: center;
    height: max-content;
    padding: 10px;
    padding-top: 50px;
    top: -50px;
}

.Experience-company {
    /* background-color: beige; */
    background-color: transparent;
    color: white;
    height: max-content;
    line-height: 50px;
    font-size: x-large;
    
}
.Experience-designation {
    /* background-color: bisque; */
    background-color: transparent;
    color: white;
    height: max-content;
    line-height: 25px;
    font-size: medium;
    padding: 10px;
}
.Experience-duration {
    /* background-color: blanchedalmond; */
    background-color: transparent;
    color: white;
    height: 25px;
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 10px;
}
.Experience-duration-time {
    /* background-color: cadetblue; */
    background-color: transparent;
    color: white;
    line-height: 25px;
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: auto;
}

.Experience-description {
    /* background-color: burlywood; */
    background-color: transparent;
    color: white;
    min-height: max-content;
    height: max-content;
    padding: 10px;
    white-space: pre-wrap;
}